html {
    background: #282c34;
    color: white;
}

.header {
    font-size: 28px;
    font-weight: bold;
    padding: 20px 100px;
    background-color: #43464b;
}
.header a {
    text-decoration: none;
    color: white;
    overflow: hidden;
}
.header div {
    margin: 10px 0 0 0;
}
.header img {
    width: 50px;
    float: left;
    margin: 5px;
}

.header a:hover img {
    animation: shake 0.5s;
    animation-iteration-count: 3; 
}
@keyframes shake {
    0% { transform: translate(2px, 2px) rotate(0deg); }
    20% { transform: translate(-2px, -2px) rotate(-3deg); }
    40% { transform: translate(2px, 0px) rotate(3deg); }
    60% { transform: translate(-2px, 2px) rotate(9deg); }
    80% { transform: translate(0px, -2px) rotate(-9deg); }
    100% { transform: translate(2px, -2px) rotate(-1deg); }
  }

.externalLink {
    float: right;
}
.externalLink img {
    width: 30px;
    margin-top: -30px;
}
.externalLink a:hover img {
    animation: shake 0.5s;
    animation-iteration-count: 3; 
}

